.legacy {

  &__title {
    font-size: 5rem;
    text-align: center;
    padding-top: 2rem;
  }

  &__form {

    padding: 5rem 3rem;
    max-width: 80%;
    margin: auto;
    width: 95%;
    background-color: var(--blanco);
    border-radius: 1rem;
    display: flex;
    flex-direction: column;

    &__div {

      display: flex;
      justify-content: space-between;
      margin-bottom: 2rem;
      align-items: center;

      &-label {
        font-size: 2rem;
        width:30%;
      }

      &-input {
        font-size: 2rem;
        padding: 1rem;
        border-radius: 1rem;
        margin: 1rem;
        width:70%;
        
      }
    }
  }
}